.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  box-sizing: border-box;
  text-align: center;
  padding-top: 50px;
  position: relative;
}

.search-form {
  text-align: center;
  width: 100%;
  background-color: #00f;
  padding: 15px;
  position: fixed;
  top: 0;
  left: 0;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.photo-card {
  border-radius: 20px;
  flex-basis: calc(25% - 7.5px);
}

.gallery img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  display: block;
}

.info {
  border: 1px solid #ededed;
  border-top: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  gap: 5px;
  display: flex;
  box-shadow: 1.95px 1.95px 2.6px rgba(0, 0, 0, .15);
}

.info-item {
  text-align: center;
}

.load-more {
  text-align: center;
  color: #fff;
  background-color: #00f;
  border-radius: 4px;
  padding: 10px 50px;
}

